.modal {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 4;
	background-color: n-hsla(var(--background-hsl-100), 0.4);
	display: flex;
	justify-content: center;
	align-items: center;

	text-align: center;

	.modal-content {
		display: block;
		background-color: n-hsla(var(--background-hsl-200));
		max-width: 100%;
		width: 70ch;

		max-height: 100%;
		height: 20em;
		margin: 2rem;
		border-radius: 4px;
		padding: 1em;

		.delete {
			@extend %delete;

			float: right;
			clear: both;
		}
	}
}

.search-modal {
	position: fixed;

	.modal-content {
		background-color: n-hsla(var(--background-hsl-200), 0.9);
		min-height: 90%;
		overflow-y: auto;
		overscroll-behavior: contain;

		scrollbar-width: none;

		&::-webkit-scrollbar {
			display: none;
		}

		a:hover,
		a:focus,
		a:focus-visible {
			background: transparent;
		}
	}

	.reader {
		font-size: 1rem;
	}
}
